import styled from 'styled-components'

const Kbd = styled.span`
  font-family: 'Menlo', 'Monaco', 'Consolas', monospace;
  border: 1px solid rgba(0, 0, 0, 0.25);
  line-height: 18px;
  border-radius: 3px;
  padding: 2px 3px;
  box-shadow: 0 1px 0 0 rgba(0, 0, 0, 0.1);
  font-weight: 400;
  font-size: 90%;
`

export default Kbd



